import React from 'react'

export const LogContext = React.createContext({ log: [] })

const propTypes = {}

export class LogProvider extends React.Component {
  state = {
    add: line => this.setState(s => ({ log: s.log.concat(line) })),
    clear: () => {
      this.setState({ log: [] })
    },
    log: [],
  }
  render() {
    return (
      <LogContext.Provider value={this.state}>
        {this.props.children}
      </LogContext.Provider>
    )
  }
}

class Log extends React.Component {
  pre = React.createRef()
  componentDidUpdate = () => {
    const { current } = this.pre
    if (!current) return
    current.scrollTop = current.scrollTop + current.offsetHeight
  }
  render() {
    return (
      <LogContext.Consumer>
        {({ clear, log }) => (
          <>
            <pre
              ref={this.pre}
              style={{ maxHeight: 300, overflow: 'auto' }}
              className="border-primary my-3"
            >
              {log.join('\n')}
            </pre>
            <button className="btn btn-secondary" onClick={clear}>
              Clear
            </button>
          </>
        )}
      </LogContext.Consumer>
    )
  }
}

Log.propTypes = propTypes

export default Log
